@font-face {
    font-display: swap;
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/roboto-v30-cyrillic_latin-regular.woff2") format("woff2"),
        url("../fonts/roboto-v30-cyrillic_latin-regular.ttf") format("truetype");
}
@font-face {
    font-display: swap;
    font-family: "Roboto";
    font-style: normal;
    font-weight: 500;
    src: url("../fonts/roboto-v30-cyrillic_latin-500.woff2") format("woff2"),
        url("../fonts/roboto-v30-cyrillic_latin-500.ttf") format("truetype");
}
@font-face {
    font-display: swap;
    font-family: "Roboto Condensed";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/roboto-condensed-v27-cyrillic_latin-regular.woff2")
            format("woff2"),
        url("../fonts/roboto-condensed-v27-cyrillic_latin-regular.ttf")
            format("truetype");
}
